import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class Navigation extends Component {
    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-dark">
                <Link className="navbar-brand text-white" to="/">PST - P101</Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                    <div className="navbar-nav ">
                        <Link className="nav-link active text-white" to="/">Estudiantes<span className="sr-only">(current)</span></Link>
                        <Link className="nav-link active text-secondary" to="/profesores" tabIndex="-1" aria-disabled="true">Profesores</Link>
                    </div>
                </div>
            </nav>
        )
    }
}
